import { BASE_URL } from "../../utils/config";

export async function createNewProduct(product) {
  const response = await fetch(`${BASE_URL}/api/admin/product-form/add-product`, {
    method: 'POST',
    credentials: "include",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify(product),
  });
  const data = await response.json();
  return { data };
}

export async function updateProduct(update) {
  const response = await fetch(`${BASE_URL}/api/admin/product-form/edit-product/${update.id}`, {
    method: 'PATCH',
    credentials: "include",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify(update),
  });
  const data = await response.json();
  return { data };
}

export async function fetchProductById(id) {
  const response = await fetch(`${BASE_URL}/api/product/${id}`, {
    method: 'GET',
    credentials: "include",
  });
  const data = await response.json();
  return { data };
}

