import React, { useEffect } from 'react';
import { useForm, } from 'react-hook-form';


const EditUserInfoModal = ({ isOpen, onClose, initialUserInfo, onSave }) => {



    const {
        register,
        handleSubmit,
        reset,
        formState: { errors },
    } = useForm({
        defaultValues: {
            username: '',
            email: ''
        }
    })

    useEffect(() => {
        if (isOpen && initialUserInfo) {
            reset({
                username: initialUserInfo.username || '',
                email: initialUserInfo.email || '',
            });
        }
    }, [isOpen, initialUserInfo, reset]);


    function toTitleCase(str) {
        return str
            .toLowerCase()
            .split(' ')
            .map(word => word.charAt(0).toUpperCase() + word.slice(1))
            .join(' ');
    }

    const onSubmit = (formData) => {
        const formattedUserName = toTitleCase(formData.username)
        onSave({ ...initialUserInfo, username: formattedUserName, email: formData.email });
        reset();

    };


    if (!isOpen) {
        return null;
    }



    return (
        <div className="fixed inset-0 bg-gray-500 bg-opacity-75 flex justify-center items-center">
            <div className="bg-white p-6 rounded-md shadow-lg w-96">
                <h2 className="text-lg font-semibold mb-4">Edit User Information</h2>
                <form onSubmit={ handleSubmit(onSubmit) } noValidate>
                    <div className="mb-4">
                        <label htmlFor="username" className="block text-gray-700 text-sm font-bold mb-2">
                            Username:
                        </label>
                        <input
                            type="text"
                            { ...register("username", { required: 'username name is required', maxLength: { value: 20, message: 'max lenght should be 20' } }) }
                            id="username"
                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                        />
                        { errors.username && (<p className='text-red-800'>*{ errors.username.message }</p>) }
                    </div>
                    <div className="mb-4">
                        <label htmlFor="email" className="block text-sm font-medium leading-6 text-gray-900">
                            Email address
                        </label>
                        <input
                            id="email"
                            { ...register("email", { required: 'email is required', pattern: { value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i, message: 'enter a valid email address' } }) }
                            type="email"
                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                        />
                        { errors.email && (<p className='text-red-800'>*{ errors.email.message }</p>) }
                    </div>

                    {/* Buttons */ }
                    <div className="flex justify-end space-x-4 pt-2">
                        <button
                            type="button"
                            onClick={ () => {
                                reset();
                                onClose();
                            } }
                            className="bg-red-500 text-white px-4 py-2 rounded-md hover:bg-red-600"
                        >
                            Cancel
                        </button>
                        <button
                            type="submit"
                            className="bg-indigo-600 text-white px-4 py-2 rounded-md hover:bg-indigo-700"
                        >
                            Edit
                        </button>
                    </div>
                </form>
            </div>
        </div>
    );
};

export default EditUserInfoModal;