import { useForm } from "react-hook-form";
import { useEffect, useState } from "react";

export default function UserAddressForm({
  modalData,
  setModalData,
  data,
  handleAddAddress,
  handleEditAddress,
}) {
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm({
    defaultValues: data,
  });


  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");

  // Pre-fill the form when editing
  useEffect(() => {
    if (modalData.addOrEdit === "edit" && data) {
      reset(data); // Pre-fill the form when editing
    } else {
      reset({});
    }
  }, [modalData.isOpen, modalData.addOrEdit, data, reset]);

  const onSubmit = (formData) => {
    if (modalData.addOrEdit === "add") {
      handleAddAddress(formData);
    } else {
      handleEditAddress(formData);
    }
    setModalData({ ...modalData, isOpen: false });
    reset();
  };

  function handleFirstName(e) {
    const value = e.target.value;
    setFirstName((value.charAt(0).toUpperCase() + value.slice(1)))
  }

  function handleLastName(e) {
    const value = e.target.value;
    setLastName((value.charAt(0).toUpperCase() + value.slice(1)))
  }


  return (
    <form onSubmit={ handleSubmit(onSubmit) } className="space-y-6" noValidate >
      {/* Contact Info */ }
      <div>
        <h3 className="text-lg font-semibold mb-2">Contact Info</h3>
        <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
          <div>
            <label htmlFor="email" className="block text-sm font-medium leading-6 text-gray-900">
              Email address
            </label>
            <input
              id="email"
              { ...register("email", { required: 'email is required', pattern: { value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i, message: 'enter a valid email address' } }) }
              type="email"
              className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
            />
            { errors.email && (<p className='text-red-800'>*{ errors.email.message }</p>) }
          </div>
          <div>
            <label htmlFor="phone" className="block text-sm font-medium leading-6 text-gray-900">
              Phone
            </label>
            <input
              type="text"
              { ...register('phone', { required: 'phone number is required', pattern: { value: /(\+?( |-|\.)?\d{1,2}( |-|\.)?)?(\(?\d{3}\)?|\d{3})( |-|\.)?(\d{3}( |-|\.)?\d{4})/g, message: 'enter a valid phone number' } }) }
              id="phone"
              className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
            />
            { errors.phone && (<p className='text-red-800'>*{ errors.phone.message }</p>) }
          </div>
        </div>
      </div>

      {/* Shipping Info */ }
      <div>
        <h3 className="text-lg font-semibold mb-2">Shipping Info</h3>
        <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">

          <div>
            <label htmlFor="first-name" className="block text-sm font-medium leading-6 text-gray-900">
              First name
            </label>
            <input
              type="text"
              { ...register("firstName", { required: 'first name is required', maxLength: { value: 10, message: 'max lenght should be 10' } }) }
              id="first-name"
              value={ firstName }
              onChange={ (e) => { handleFirstName(e) } }
              className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
            />
            { errors.firstName && (<p className='text-red-800'>*{ errors.firstName.message }</p>) }
          </div>

          <div>
            <label htmlFor="last-name" className="block text-sm font-medium leading-6 text-gray-900">
              Last name
            </label>
            <input
              type="text"
              { ...register("lastName", { required: 'last name is required', maxLength: 10 }) }
              value={ lastName }
              onChange={ (e) => handleLastName(e) }
              id="last-name"
              className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
            />
            { errors.lastName && (<p className='text-red-800'>*{ errors.lastName.message }</p>) }
          </div>


          <div>
            <label htmlFor="address" className="block text-sm font-medium leading-6 text-gray-900">
              Address
            </label>
            <input
              type="text"
              { ...register('address', { required: 'address is required' }) }
              id="address"
              className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
            />
            { errors.address && (<p className='text-red-800'>*{ errors.address.message }</p>) }

          </div>


          <div>
            <label htmlFor="appartment-address" className="block text-sm font-medium leading-6 text-gray-900">
              Apartment, suite, etc.
            </label>
            <input
              type="text"
              { ...register('appartment', { required: 'appartment/suite is required' }) }
              id="appartment-address"
              className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
            />
            { errors.appartment && (<p className='text-red-800'>*{ errors.appartment.message }</p>) }

          </div>
          <div>
            <label htmlFor="city" className="block text-sm font-medium leading-6 text-gray-900">
              City
            </label>
            <input
              type="text"
              { ...register('city', { required: 'city is required', maxLength: 13 }) }
              id="city"
              className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
            />
            { errors.city && (<p className='text-red-800'>*{ errors.city.message }</p>) }
          </div>

          <div>
            <label htmlFor="state" className="block text-sm font-medium leading-6 text-gray-900">
              State / Province
            </label>
            <input
              type="text"
              { ...register('state', { required: 'state is required', maxLength: 13 }) }
              id="state"

              className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
            />
            { errors.state && (<p className='text-red-800'>*{ errors.state.message }</p>) }
          </div>
          <div>
            <label htmlFor="zip" className="block text-sm font-medium leading-6 text-gray-900">
              ZIP / Postal code
            </label>
            <input
              type="text"
              inputMode="numeric"  // This ensures a numeric keypad on mobile devices
              { ...register("zip", {
                required: "Zip Code is required",  // Optional: if you want to make it mandatory
                pattern: {
                  value: /^[0-9]{5,6}$/,  // Ensures only digits, and limits the number to 5-6 digits
                  message: "Please enter a valid zip code"  // Custom error message
                }
              }) }
              className="input-style"
              placeholder="Enter your zip code"
              onKeyPress={ (e) => {
                // Prevent the user from typing non-numeric characters
                if (!/[0-9]/.test(e.key)) {
                  e.preventDefault();
                }
              } }
            />
            { errors.zip && (<p className='text-red-800'>*{ errors.zip.message }</p>) }
          </div>

          <div>
            <label htmlFor="country" className="block text-sm font-medium leading-6 text-gray-900">
              Country
            </label>
            <select
              id="country"
              { ...register('country', { required: 'Country is required' }) }
              name="country"
              className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:max-w-xs sm:text-sm sm:leading-6"
            >
              <option>India</option>
              <option>United States</option>
              <option>Canada</option>
              <option>Mexico</option>
            </select>
            { errors.country && (<p className='text-red-800'>*{ errors.country.message }</p>) }

          </div>
        </div>
      </div>

      {/* Buttons */ }
      <div className="flex justify-end space-x-4 pt-2">
        <button
          type="button"
          onClick={ () => {
            setModalData({ ...modalData, isOpen: false });
            reset();
          } }
          className="bg-red-500 text-white px-4 py-2 rounded-md hover:bg-red-600"
        >
          Cancel
        </button>
        <button
          type="submit"
          className="bg-indigo-600 text-white px-4 py-2 rounded-md hover:bg-indigo-700"
        >
          { modalData?.addOrEdit === 'edit' ? 'Edit' : 'Add' }
        </button>
      </div>
    </form >
  );
}
