// ProductList.jsx
import { Fragment, useEffect, useState } from 'react'
import { Dialog, Disclosure, Menu, Transition } from '@headlessui/react'
import { XMarkIcon } from '@heroicons/react/24/outline'
import {
  ChevronDownIcon,
  FunnelIcon,
  MinusIcon,
  PlusIcon,
  StarIcon,
} from '@heroicons/react/20/solid'
import { Link } from 'react-router-dom'
import {
  selectAllProducts,
  selectTotalItems,
  selectCategory,
  selectBrand,
  fetchProductByFilterAsync,
  fetchCategoriesAsync,
  fetchBrandsAsync,
  selectProductSliceStatus,
} from '../productSlice'
import { useSelector, useDispatch } from 'react-redux'
import { ITEMS_PER_PAGE } from '../../../app/constants'
import ProductListSkeleton from '../../comman/SkeletonLoading/ProductListSkeleton'
import FeatureCarousel from '../../comman/FeatureCarousel'
import noProductImage from '../../../assets/images/noProduct.jpg'
import Pagination from '../../comman/Pagination'
import { getDiscountedPrice } from '../../../utils/functions/getDiscountedPrice'
import ScrollToTopButton from '../../comman/ScrollToTopButton'

const sortOptions = [
  { name: 'Best Rating', sort: 'rating', order: 'desc', current: false },
  { name: 'Price: Low to High', sort: 'price', order: 'asc', current: false },
  { name: 'Price: High to Low', sort: 'price', order: 'desc', current: false },
]

function classNames(...classes) {
  return classes.filter(Boolean).join(' ')
}

function ProductList() {
  const dispatch = useDispatch()
  const products = useSelector(selectAllProducts)
  const totalItems = useSelector(selectTotalItems)
  const category = useSelector(selectCategory)
  const brand = useSelector(selectBrand)
  const status = useSelector(selectProductSliceStatus)

  const [selectedFilters, setSelectedFilters] = useState({})
  const [sort, setSort] = useState({})
  const [page, setPage] = useState(1)
  const [mobileFiltersOpen, setMobileFiltersOpen] = useState(false)

  const handleFilter = (e, section, option) => {
    const { checked } = e.target
    const updated = { ...selectedFilters }
    if (!updated[section.id]) updated[section.id] = []
    if (checked) {
      updated[section.id].push(option.value)
    } else {
      updated[section.id] = updated[section.id].filter(v => v !== option.value)
      if (updated[section.id].length === 0) delete updated[section.id]
    }
    setSelectedFilters(updated)
    setPage(1)
  }

  const handleSort = (option) => {
    if (!option) {
      setSort({})
      sortOptions.forEach(o => (o.current = false))
    } else {
      setSort({ _sort: option.sort, _order: option.order })
      sortOptions.forEach(o => (o.current = o.name === option.name))
    }
    setPage(1)
  }

  useEffect(() => {
    const pagination = { _page: page, _limit: ITEMS_PER_PAGE }
    dispatch(fetchProductByFilterAsync({ filter: selectedFilters, sort, pagination }))
  }, [dispatch, selectedFilters, sort, page])

  useEffect(() => {
    dispatch(fetchBrandsAsync())
    dispatch(fetchCategoriesAsync())
  }, [dispatch])

  useEffect(() => {
    setPage(1)
  }, [totalItems, sort])

  const filters = [
    { id: 'brand', name: 'Brands', options: brand },
    { id: 'category', name: 'Category', options: category },
  ]

  return (
    <>
      <aside><FeatureCarousel /></aside>
      <div className="bg-white">
        <Transition.Root show={ mobileFiltersOpen } as={ Fragment }>
          <Dialog as="div" className="relative z-40 lg:hidden" onClose={ setMobileFiltersOpen }>
            <Transition.Child
              as={ Fragment }
              enter="transition-opacity ease-linear duration-300"
              enterFrom="opacity-0"
              enterTo="opacity-100"
              leave="transition-opacity ease-linear duration-300"
              leaveFrom="opacity-100"
              leaveTo="opacity-0"
            >
              <div className="fixed inset-0 bg-black bg-opacity-25" />
            </Transition.Child>

            <div className="fixed inset-0 z-40 flex">
              <Transition.Child
                as={ Fragment }
                enter="transition ease-in-out duration-300 transform"
                enterFrom="translate-x-full"
                enterTo="translate-x-0"
                leave="transition ease-in-out duration-300 transform"
                leaveFrom="translate-x-0"
                leaveTo="translate-x-full"
              >
                <Dialog.Panel className="relative ml-auto flex h-full w-full max-w-xs flex-col overflow-y-auto bg-white py-4 pb-12 shadow-xl">
                  <div className="flex items-center justify-between px-4">
                    <h2 className="text-lg font-medium text-gray-900">Filters</h2>
                    <button
                      type="button"
                      className="-mr-2 flex h-10 w-10 items-center justify-center rounded-md bg-white p-2 text-gray-400"
                      onClick={ () => setMobileFiltersOpen(false) }
                    >
                      <XMarkIcon className="h-6 w-6" aria-hidden="true" />
                    </button>
                  </div>

                  {/* Mobile filter list */ }
                  <form className="mt-4 border-t border-gray-200">
                    { filters.map((section) => (
                      <Disclosure as="div" key={ section.id } className="border-t border-gray-200 px-4 py-6">
                        { ({ open }) => (
                          <>
                            <h3 className="-mx-2 -my-3 flow-root">
                              <Disclosure.Button className="flex w-full items-center justify-between bg-white px-2 py-3 text-gray-400 hover:text-gray-500">
                                <span className="font-medium text-gray-900">{ section.name }</span>
                                <span className="ml-6 flex items-center">
                                  { open ? (
                                    <MinusIcon className="h-5 w-5" aria-hidden="true" />
                                  ) : (
                                    <PlusIcon className="h-5 w-5" aria-hidden="true" />
                                  ) }
                                </span>
                              </Disclosure.Button>
                            </h3>
                            <Disclosure.Panel className="pt-6">
                              <div className="space-y-6">
                                { section.options.map((option, optionIdx) => (
                                  <div key={ option.value } className="flex items-center">
                                    <input
                                      id={ `filter-${section.id}-${optionIdx}` }
                                      name={ `${section.id}[]` }
                                      defaultValue={ option.value }
                                      type="checkbox"
                                      onChange={ (e) => handleFilter(e, section, option) }
                                      className="h-4 w-4 rounded border-gray-300 text-indigo-600 focus:ring-indigo-500"
                                    />
                                    <label htmlFor={ `filter-${section.id}-${optionIdx}` } className="ml-3 text-sm text-gray-600">
                                      { option.label }
                                    </label>
                                  </div>
                                )) }
                              </div>
                            </Disclosure.Panel>
                          </>
                        ) }
                      </Disclosure>
                    )) }
                  </form>
                </Dialog.Panel>
              </Transition.Child>
            </div>
          </Dialog>
        </Transition.Root>

        <main className="mx-auto max-w-7xl px-4 sm:px-6 lg:px-8">
          <div className="flex items-baseline justify-between border-b border-gray-200 pb-6 pt-14">
            <h1 className="text-xl sm:text-3xl font-bold tracking-tight text-gray-900">
              New Arrivals
            </h1>

            <div className="flex items-center">
              <Menu as="div" className="relative inline-block text-left">
                <Menu.Button className="group inline-flex justify-center text-sm font-medium text-gray-700 hover:text-gray-900">
                  Sort
                  <ChevronDownIcon
                    className="-mr-1 ml-1 h-5 w-5 text-gray-400 group-hover:text-gray-500"
                    aria-hidden="true"
                  />
                </Menu.Button>

                <Transition
                  as={ Fragment }
                  enter="transition ease-out duration-100"
                  enterFrom="transform opacity-0 scale-95"
                  enterTo="transform opacity-100 scale-100"
                  leave="transition ease-in duration-75"
                  leaveFrom="transform opacity-100 scale-100"
                  leaveTo="transform opacity-0 scale-95"
                >
                  <Menu.Items className="absolute right-0 z-10 mt-2 w-48 origin-top-right rounded-md bg-white shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none">
                    <div className="py-1">
                      <Menu.Item>
                        { ({ active }) => (
                          <p
                            onClick={ () => handleSort(null) }
                            className={ classNames(
                              active ? 'bg-gray-100' : '',
                              'block px-4 py-2 text-sm text-gray-500 cursor-pointer'
                            ) }
                          >
                            Reset Sort
                          </p>
                        ) }
                      </Menu.Item>
                      { sortOptions.map((option) => (
                        <Menu.Item key={ option.name }>
                          { ({ active }) => (
                            <p
                              onClick={ () => handleSort(option) }
                              className={ classNames(
                                option.current ? 'font-medium text-gray-900' : 'text-gray-500',
                                active ? 'bg-gray-100' : '',
                                'block px-4 py-2 text-sm cursor-pointer'
                              ) }
                            >
                              { option.name }
                            </p>
                          ) }
                        </Menu.Item>
                      )) }
                    </div>
                  </Menu.Items>
                </Transition>
              </Menu>

              <button
                onClick={ () => setMobileFiltersOpen(true) }
                className="-m-2 ml-4 p-2 text-gray-400 hover:text-gray-500 sm:ml-6 lg:hidden"
              >
                <FunnelIcon className="h-5 w-5" aria-hidden="true" />
              </button>
            </div>
          </div>

          <section aria-labelledby="products-heading" className="pb-24 pt-6">
            <div className="grid grid-cols-1 gap-x-8 gap-y-10 lg:grid-cols-4">
              <form className="hidden lg:block">
                { filters.map((section) => (
                  <Disclosure as="div" key={ section.id } className="border-b border-gray-200 py-6">
                    { ({ open }) => (
                      <>
                        <h3 className="-my-3 flow-root">
                          <Disclosure.Button className="flex w-full items-center justify-between bg-white py-3 text-sm text-gray-400 hover:text-gray-500">
                            <span className="font-medium text-gray-900">{ section.name }</span>
                            <span className="ml-6 flex items-center">
                              { open ? (
                                <MinusIcon className="h-5 w-5" aria-hidden="true" />
                              ) : (
                                <PlusIcon className="h-5 w-5" aria-hidden="true" />
                              ) }
                            </span>
                          </Disclosure.Button>
                        </h3>
                        <Disclosure.Panel className="pt-6">
                          <div className="space-y-4">
                            { section.options.map((option, optionIdx) => (
                              <div key={ option.value } className="flex items-center">
                                <input
                                  id={ `filter-${section.id}-${optionIdx}` }
                                  name={ `${section.id}[]` }
                                  defaultValue={ option.value }
                                  type="checkbox"
                                  onChange={ (e) => handleFilter(e, section, option) }
                                  className="h-4 w-4 rounded border-gray-300 text-indigo-600 focus:ring-indigo-500"
                                />
                                <label htmlFor={ `filter-${section.id}-${optionIdx}` } className="ml-3 text-sm text-gray-600">
                                  { option.label }
                                </label>
                              </div>
                            )) }
                          </div>
                        </Disclosure.Panel>
                      </>
                    ) }
                  </Disclosure>
                )) }
              </form>

              <div className="lg:col-span-3">
                { status === 'loading' ? (
                  <ProductListSkeleton />
                ) : products.length > 0 ? (
                  <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
                    { products.map((product) => (
                      <div key={ product.id } className="border p-4 rounded shadow-sm relative">
                        <Link to={ `/product/${product.id}` }>
                          <div className="relative group overflow-hidden">
                            <img
                              src={ product.thumbnail || noProductImage }
                              alt={ product.title }
                              className="w-full h-auto max-h-60 object-contain rounded mb-2 transform transition-transform duration-300 group-hover:scale-105"
                            />
                            { product.discountPercentage > 0 && (
                              <span className="absolute top-2 left-2 bg-red-500 text-white text-xs font-semibold px-2 py-1 rounded">
                                { product.discountPercentage }% OFF
                              </span>
                            ) }
                          </div>


                          <h3 className="text-sm font-semibold mt-2">{ product.title }</h3>

                          <div className="flex items-center mt-1">
                            <StarIcon className="h-4 w-4 text-yellow-400 mr-1" />
                            <span className="text-xs text-gray-500">{ product.rating }</span>
                          </div>

                          <div className="mt-2">
                            <span className="text-gray-900 font-semibold">
                              ₹{ getDiscountedPrice(product.price, product.discountPercentage) }
                            </span>
                            { product.discountPercentage > 0 && (
                              <span className="text-xs text-gray-500 line-through ml-2">
                                ₹{ product.price }
                              </span>
                            ) }
                          </div>
                        </Link>
                      </div>
                    )) }

                  </div>
                ) : (
                  <div className="text-center py-10">
                    <img src={ noProductImage } alt="No Products" className="w-60 mx-auto" />
                    <p className="text-gray-500 mt-4">No products found</p>
                  </div>
                ) }
                <ScrollToTopButton />
                <Pagination page={ page } totalItems={ totalItems } handlePage={ setPage } />
              </div>
            </div>
          </section>
        </main>
      </div>
    </>
  )
}

export default ProductList
