import { useState } from 'react';

export default function Slider({ images }) {
    const [currentImageIndex, setCurrentImageIndex] = useState(0);
    const [imageLoaded, setImageLoaded] = useState(false);

    const handleDotClick = (index) => {
        setCurrentImageIndex(index);
        setImageLoaded(false); // Reset loading state for new image
    };

    const goToNextImage = () => {
        setCurrentImageIndex((prevIndex) => {
            setImageLoaded(false);
            return (prevIndex + 1) % images.length;
        });
    };

    const goToPrevImage = () => {
        setCurrentImageIndex((prevIndex) => {
            setImageLoaded(false);
            return (prevIndex - 1 + images.length) % images.length;
        });
    };

    return (
        <div className="relative w-full">
            {/* Prev Button */ }
            <button
                onClick={ goToPrevImage }
                className="absolute left-2 top-1/2 transform -translate-y-1/2 text-white bg-indigo-600 hover:bg-indigo-700 p-2 rounded-full shadow-lg z-10"
            >
                &lt;
            </button>

            {/* Skeleton Loader or Image */ }
            <div className="w-full aspect-video rounded-lg overflow-hidden bg-gray-100">
                { !imageLoaded && (
                    <div className="w-full h-full animate-pulse bg-gray-300"></div>
                ) }
                <img
                    src={ images[currentImageIndex] }
                    alt={ `productImage-${currentImageIndex}` }
                    className={ `object-cover object-center w-full h-full transition-opacity duration-300 ${imageLoaded ? 'opacity-100' : 'opacity-0'}` }
                    onLoad={ () => setImageLoaded(true) }
                />
            </div>

            {/* Next Button */ }
            <button
                onClick={ goToNextImage }
                className="absolute right-2 top-1/2 transform -translate-y-1/2 text-white bg-indigo-600 hover:bg-indigo-700 p-2 rounded-full shadow-lg z-10"
            >
                &gt;
            </button>

            {/* Dots Navigation */ }
            <div className="absolute bottom-2 left-1/2 transform -translate-x-1/2 flex space-x-2 z-10">
                { images.map((_, index) => (
                    <button
                        key={ index }
                        onClick={ () => handleDotClick(index) }
                        className={ `h-2 w-2 rounded-full ${currentImageIndex === index ? 'bg-indigo-600' : 'bg-gray-300'}` }
                    />
                )) }
            </div>
        </div>
    );
}
