// App.jsx
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {
  createBrowserRouter,
  RouterProvider,
} from 'react-router-dom';

import {
  checkAuthAsync,
  selectLoggedInUserToken,
  selectUserChecked,
} from './features/auth/authSlice';

import { fetchCartItemsAsync } from './features/cart/cartSlice';
import {
  fetchLoggedInUserInfoAsync,
  fetchOrdersByUserAsync,
} from './features/user/userSlice';

import HomePage from './features/pages/HomePage';
import LoginPage from './features/pages/LoginPage';
import SignupPage from './features/pages/SignupPage';
import CartPage from './features/pages/CartPage';
import CheckoutPage from './features/pages/CheckoutPage';
import ProtectedRoute from './features/auth/components/ProtectedRoute';
import UserOrderPage from './features/pages/UserOrderPage';
import UserProfilePage from './features/pages/userProfilePage';
import NotFound404 from './features/pages/NotFound404';
import Logout from './features/auth/components/Logout';
import ForgetPassword from './features/auth/components/ForgetPassword';
import ProtectedAdmin from './features/auth/components/ProtectedAdmin';
import AdminHomePage from './features/pages/AdminHomePage';
import AdminProductFormPage from './features/pages/AdminProductFormPage';
import AdminOrdersPage from './features/pages/AdminOrdersPage';
import OrderSuccessPage from './features/pages/OrderSuccessPage';
import PaymentError from './features/pages/PaymentErrorPage';
import ResetPassword from './features/auth/components/ResetPassword';
import ScrollToTopLayout from './features/comman/SrcollToTopLayout';
import Preloading from './features/comman/Preloading';
import { Toaster } from 'react-hot-toast';
import ProductDetailPage from './features/pages/ProductDetailPage';


// card payments configs
import { loadStripe } from '@stripe/stripe-js';
import { Elements } from '@stripe/react-stripe-js';

// Replace with your Stripe public key
const stripePromise = loadStripe('pk_test_51NvJwwSBLTNKtMk3SqVAUARDUNVJrpGNswpGR31aSGuRoqZLRXcXEnCvKiKsVUF73Wj3fOOIyQSJVIE2ur0Gexwl00iZmgDlAR');

const router = createBrowserRouter([
  {
    path: '/',
    element: <ScrollToTopLayout />,
    children: [
      {
        index: true,
        element: (
          <ProtectedRoute>
            <HomePage />
          </ProtectedRoute>
        ),
      },
      { path: 'login', element: <LoginPage /> },
      { path: 'signup', element: <SignupPage /> },
      {
        path: 'cart',
        element: (
          <ProtectedRoute>
            <CartPage />
          </ProtectedRoute>
        ),
      },
      {
        path: 'checkout',
        element: (
          <ProtectedRoute>
            <CheckoutPage />
          </ProtectedRoute>
        ),
      },
      {
        path: '/product/:id',
        element: (
          <ProtectedRoute>
            <ProductDetailPage />
          </ProtectedRoute>
        ),
      },
      {
        path: 'order-success/:id',
        element: (
          <ProtectedRoute>
            <OrderSuccessPage />
          </ProtectedRoute>
        ),
      },
      {
        path: 'user-orders',
        element: (
          <ProtectedRoute>
            <UserOrderPage />
          </ProtectedRoute>
        ),
      },
      {
        path: 'user-profile',
        element: (
          <ProtectedRoute>
            <UserProfilePage />
          </ProtectedRoute>
        ),
      },
      {
        path: 'order-cancalled',
        element: (
          <ProtectedRoute>
            <PaymentError />
          </ProtectedRoute>
        ),
      },
      { path: 'logout', element: <Logout /> },
      { path: 'reset-password-request', element: <ForgetPassword /> },
      { path: 'reset-password', element: <ResetPassword /> },
      {
        path: 'admin',
        element: (
          <ProtectedAdmin>
            <AdminHomePage />
          </ProtectedAdmin>
        ),
      },
      {
        path: 'admin/product-form/add',
        element: (
          <ProtectedAdmin>
            <AdminProductFormPage />
          </ProtectedAdmin>
        ),
      },
      {
        path: 'admin/product-form/edit/:id',
        element: (
          <ProtectedAdmin>
            <AdminProductFormPage />
          </ProtectedAdmin>
        ),
      },
      {
        path: 'admin/orders',
        element: (
          <ProtectedAdmin>
            <AdminOrdersPage />
          </ProtectedAdmin>
        ),
      },
      { path: '*', element: <NotFound404 /> },
    ],
  },
]);

function App() {
  const dispatch = useDispatch();
  const user = useSelector(selectLoggedInUserToken);
  const userChecked = useSelector(selectUserChecked);
  const [isLoading, setIsLoading] = useState(true);

  // Check if user is authenticated on first load
  useEffect(() => {
    dispatch(checkAuthAsync()).unwrap().catch(() => {
      // Optional: toast.error("Session expired") or silent
    });
  }, [dispatch]);

  // Fetch user-related data if authenticated
  useEffect(() => {
    if (user && userChecked) {
      dispatch(fetchCartItemsAsync());
      dispatch(fetchLoggedInUserInfoAsync());
      dispatch(fetchOrdersByUserAsync());
    }
  }, [dispatch, user]);

  // Control preloader visibility
  useEffect(() => {
    if (userChecked) {
      setIsLoading(false);
    } else {
      const timer = setTimeout(() => {
        setIsLoading(false);
      }, 3000);
      return () => clearTimeout(timer);
    }
  }, [userChecked]);

  return (
    <div>
      <Toaster
        position="top-center"
        reverseOrder={ false }
        toastOptions={ {
          duration: 5000,
          style: { background: 'white', color: 'black' },
          success: {
            duration: 3000,
            theme: { primary: 'green', secondary: 'black' },
          },
          error: {
            duration: 3000,
            theme: { primary: 'red', secondary: 'black' },
          },
          loading: {
            theme: { primary: 'green', secondary: 'black' },
          },
        } }
      />

      { isLoading ? (
        <Preloading />
      ) : (
        <Elements stripe={ stripePromise }>
          <RouterProvider router={ router } />
        </Elements>)
      }
    </div>
  );
}

export default App;
