import { BASE_URL } from "../../utils/config";

export function fetchOrdersByUser() {
  return new Promise(async (resolve, reject) => {
    try {
      const response = await fetch(`${BASE_URL}/api/orders/`, {
        method: 'GET',
        credentials: 'include',
      });
      const data = await response.json();
      resolve({ data });
    } catch (error) {
      reject(error); // Important! Reject to prevent loop
    }
  });
}

export function updateUser(update, type) {
  return new Promise(async (resolve, reject) => {
    try {
      const response = await fetch(`${BASE_URL}/api/users/`, {
        method: 'PATCH',
        body: JSON.stringify(update),
        headers: {
          "Content-Type": 'application/json',
        },
        credentials: 'include',
      });
      const data = await response.json();

      if (!response.ok) {
        if (type === 'ADD') return reject(new Error('Failed to Add'));
        if (type === 'REMOVE') return reject(new Error('Failed to Remove'));
        if (type === 'EDIT') return reject(new Error('Failed to Edit'));
      }

      resolve({ data });
    } catch (error) {
      reject(error);
    }
  });
}

