import { useDispatch, useSelector } from "react-redux"
import { fetchCartItemsAsync, removeFromCartAsync, updateCartAsync } from "../../cart/cartSlice"
import { getDiscountedPrice } from "../../../utils/functions/getDiscountedPrice"
import Modal from '../../comman/Modal';
import { useEffect, useState } from "react"
import { selectLoggedInUserToken } from "../../auth/authSlice"
import toast from "react-hot-toast";




export default function CheckoutCart({ cartItems, subTotal, subTotalWithDiscount, totalQunatity, isOpen, setIsOpen, handleCancleDelete }) {
    const [selectedDeleteCartItem, setSelectedDeleteCartItem] = useState(null);
    const user = useSelector(selectLoggedInUserToken);
    const dispatch = useDispatch();

    useEffect(() => {
        if (user) {
            dispatch(fetchCartItemsAsync());
        }
    }, [dispatch, user]);

    function handleRemoveFromCart(itemId) {
        setSelectedDeleteCartItem(itemId);
        setIsOpen(true);
    }

    function handleConfirmDeleteCartItem() {
        dispatch(removeFromCartAsync({ itemId: selectedDeleteCartItem, toast }));
        setIsOpen(false);
        setSelectedDeleteCartItem(-1);
    }

    function handleUpdateCart(e, item) {
        dispatch(updateCartAsync({ id: item.id, quantity: e.target.value }));
    }

    return (
        <div className="bg-white p-5 mt-5 rounded-lg shadow-lg flex flex-col justify-between h-full">
            <h1 className="text-xl font-semibold leading-7 mb-6 border-b border-gray-200 pb-2">Shopping Cart</h1>

            <div className="flex-1 overflow-auto">
                <ul className="-my-6 divide-y divide-gray-200">
                    { cartItems.map((item) => (
                        <li key={ item.product.id } className="flex py-6 flex-col sm:flex-row">
                            <div className="h-24 w-24 flex-shrink-0 overflow-hidden rounded-md border border-gray-200">
                                <img
                                    src={ item.product.images[0] }
                                    alt={ "itemImage" }
                                    className="h-full w-full object-contain object-center bg-white"
                                />
                            </div>

                            <div className="ml-4 flex flex-1 flex-col">
                                <div className="flex flex-col xs:flex-row justify-between text-base font-medium text-gray-900">
                                    <div>
                                        <h4>{ item.product.title }</h4>
                                    </div>
                                    <div>
                                        <p className="text-sm font-medium text-gray-900">
                                            ₹{ getDiscountedPrice(item.product.price, item.product.discountPercentage) }
                                        </p>

                                        <p className="text-sm font-medium text-gray-500 line-through">₹{ item.product.price }</p>
                                    </div>
                                </div>

                                <div className="flex flex-col xs:flex-row flex-1 items-start xs:items-end justify-between text-sm mt-2">
                                    <p className="text-gray-500">
                                        Qty
                                        <select
                                            className="mx-3 my-3 border rounded px-2 py-1 text-sm"
                                            onChange={ (e) => handleUpdateCart(e, item) }
                                            value={ item.quantity }
                                        >
                                            { item.product.stock >= 1 && <option value={ 1 }>1</option> }
                                            { item.product.stock >= 2 && <option value={ 2 }>2</option> }
                                            { item.product.stock >= 3 && <option value={ 3 }>3</option> }
                                            { item.product.stock >= 4 && <option value={ 4 }>4</option> }
                                            { item.product.stock >= 5 && <option value={ 5 }>5</option> }
                                        </select>
                                    </p>

                                    { selectedDeleteCartItem === item.id && (
                                        <Modal
                                            message={ `Are you sure you want to delete?` }
                                            title={ `Delete ${item.product.title}` }
                                            isOpen={ isOpen }
                                            buttonOne="Delete"
                                            buttonTwo="Cancel"
                                            buttonOneHandler={ handleConfirmDeleteCartItem }
                                            buttonTwoHandler={ handleCancleDelete }
                                        />
                                    ) }

                                    <div className="flex">
                                        <button
                                            type="button"
                                            onClick={ () => {
                                                handleRemoveFromCart(item.id);
                                            } }
                                            className="font-medium text-indigo-600 hover:text-indigo-500"
                                        >
                                            Remove
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </li>
                    )) }
                </ul>
            </div>

            {/* Summary Section */ }
            <div className="border-t border-gray-200 px-4 py-6 sm:px-6 mt-6">
                <div className="flex text-base font-medium text-gray-900 justify-between items-baseline mb-2">
                    <p>Total Quantity</p>
                    <p className="text-sm font-medium">{ totalQunatity > 1 ? `${totalQunatity} items` : `${totalQunatity} item` }</p>
                </div>
                <div className="flex text-base font-medium text-gray-900 justify-between items-baseline mb-2">
                    <p>Subtotal</p>
                    <div className="flex space-x-3">
                        <p className="text-sm font-medium bg-yellow-200 px-2 py-1 rounded">₹{ subTotalWithDiscount }</p>
                    </div>
                </div>
                <div className="flex text-base font-medium text-gray-900 justify-between items-baseline">
                    <p>You Saved</p>
                    <p className="text-sm font-medium text-gray-500">
                        ₹{ (Math.round((subTotal - subTotalWithDiscount) * 100) / 100).toFixed(2) }
                    </p>
                </div>
            </div>
        </div>
    );
}
