import { useDispatch, useSelector } from "react-redux";
import { logoutAsync, selectLoggedInUserToken } from "../authSlice";
import { Navigate } from "react-router-dom";
import { useEffect, useState, useRef } from "react";
import toast from "react-hot-toast";

export default function Logout() {
    const dispatch = useDispatch();
    const user = useSelector(selectLoggedInUserToken);
    const [loggedOut, setLoggedOut] = useState(false);
    
    const hasLogoutRan = useRef(false); 

    useEffect(() => {
        if (hasLogoutRan.current) return; 

        const logoutNow = async () => {
            if (user && !loggedOut) {
                try {
                    await dispatch(logoutAsync()).unwrap();
                    toast.success("Logged out successfully");
                    setLoggedOut(true); // After logout, mark loggedOut as true
                } catch (err) {
                    toast.error("Logout failed");
                }
            }
        };

        if (!loggedOut) {
            logoutNow(); 
            hasLogoutRan.current = true; 
        }
    }, [dispatch, user, loggedOut]);

    if (loggedOut) {
        return <Navigate to="/" replace />;
    }

    return null;
}
