import { getDiscountedPrice } from "./getDiscountedPrice";

export default function getSubTotalWithDiscount(cartItems) {
    const total = cartItems.reduce((sum, item) => {
        const discountedPrice = getDiscountedPrice(item.product.price, item.product.discountPercentage);
        return sum + item.quantity * discountedPrice;
    }, 0);
    return total.toFixed(2);
}
