import { useSelector, useDispatch } from "react-redux";
import { selectLoggedInUserToken } from "../authSlice";
import { Navigate, useLocation } from "react-router-dom";
import toast from "react-hot-toast";
import { useEffect, useState } from "react";
import { checkAuthAsync, logoutUser, selectManualLogoutOrNot, resetManualLogout } from "../authSlice";


export default function ProtectedRoute({ children }) {
    const userToken = useSelector(selectLoggedInUserToken);
    const dispatch = useDispatch();
    const [authFailed, setAuthFailed] = useState(false);
    const manualLogout = useSelector(selectManualLogoutOrNot);

    const location = useLocation();
    useEffect(() => {
        const verifyAuth = async () => {
            if (!userToken || manualLogout) {
                setAuthFailed(true);

                // reset manual logout flag after triggering redirect
                dispatch(resetManualLogout());

                return;
            }

            try {
                await dispatch(checkAuthAsync()).unwrap();
            } catch (error) {
                toast.error("Session expired. Please log in again!");
                dispatch(logoutUser());
                setAuthFailed(true);
            }
        };

        verifyAuth();
    }, [location.pathname, manualLogout, dispatch]);


    if (authFailed) {
        return <Navigate to="/login" replace />;
    }

    return children;
}
