import { BASE_URL } from "../../utils/config";

export async function addOrder(orderDetail) {
  const response = await fetch(`${BASE_URL}/api/orders/`, {
    method: 'POST',
    credentials: "include",
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify(orderDetail),
  });
  const data = await response.json();
  return { data };
}


export function fetchAllOrders(pagination) {
  return new Promise(async (resolve) => {
    let queryString = '';
    for (const key in pagination) {
      queryString += `${key}=${pagination[key]}&`; // Example: _page=3&_limit=10&
    }

    const response = await fetch(`${BASE_URL}/api/admin/orders?${queryString}`, {
      method: 'GET',
      credentials: "include",
    });
    const data = await response.json();
    const totalOrders = response.headers.get('X-Total-Count');
    resolve({ data: { allOrders: data, totalOrders: +totalOrders } });
  });
}

export function updateOrderById(update) {
  return new Promise(async (resolve) => {
    const response = await fetch(`${BASE_URL}/api/admin/orders/${update.id}`, {
      method: 'PATCH',
      body: JSON.stringify({ status: update.status }),
      credentials: "include",
      headers: {
        "Content-Type": 'application/json',
      }
    });
    const data = await response.json();
    resolve({ data });
  });
}
