import { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { selectedProductById, fetchProductByIdAsync,  } from '../productSlice';
import { addToCartAsync, selectItems } from '../../cart/cartSlice';
import { toast } from "react-hot-toast";
import { StarIcon } from '@heroicons/react/20/solid';
import Slider from '../../comman/Slider';
import { getDiscountedPrice } from '../../../utils/functions/getDiscountedPrice';

export default function ProductDetail({ productId }) {
  const [imageLoaded, setImageLoaded] = useState(false);
  const selectedProduct = useSelector(selectedProductById);
  const cartItems = useSelector(selectItems);
  const dispatch = useDispatch();

  const handleAddToCart = () => {
    if (selectedProduct && cartItems.findIndex((cartItem) => cartItem.product.id === selectedProduct?.id) < 0) {
      const newProduct = { product: selectedProduct.id, quantity: 1 };
      dispatch(addToCartAsync({ item: newProduct, toast }));
    } else {
      toast.error('Item Already in Cart');
    }
  };

  useEffect(() => {
    if (productId) {
      dispatch(fetchProductByIdAsync(productId));
    }
  }, [dispatch, productId]);


  const handleImageLoaded = () => {
    setImageLoaded(true);
  };

  const imagesToDisplay = selectedProduct && Array.isArray(selectedProduct.images) ? selectedProduct.images : [];

  return selectedProduct ? (
    <div className="bg-white p-8">
      {/* Top Section: Slider + Info */ }
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
        {/* Product Images */ }
        <div className="w-full">
          { imagesToDisplay.length > 1 ? (
            <Slider images={ imagesToDisplay } />
          ) : imagesToDisplay.length === 1 ? (
            <img
              src={ imagesToDisplay[0] }
              alt="productImage"
              className={ `object-cover object-center w-full rounded-lg ${imageLoaded ? 'block' : 'hidden'}` }
              onLoad={ handleImageLoaded }
            />
          ) : null }
        </div>

        {/* Product Info */ }
        <div className="flex flex-col justify-between">
          <div className='xl:space-y-3'>
            <h2 className="text-xl md:text-3xl xl:text-5xl font-semibold text-gray-800">{ selectedProduct.title }</h2>

            <p className="text-xl md:text-2xl xl:text-5xl text-gray-900">₹{ getDiscountedPrice(selectedProduct.price, selectedProduct.discountPercentage) }</p>
            <p className="text-md md:text-lg xl:text-4xl text-gray-500 line-through">₹{ selectedProduct.price }</p>
            <div className="flex items-center">
              { [0, 1, 2, 3, 4].map((_, index) => (
                <StarIcon
                  key={ index }
                  className={ `h-5 w-5 xl:h-8 xl:w-8 ${Math.round(selectedProduct.rating) > index ? 'text-yellow-500' : 'text-gray-300'}` }
                />
              )) }
              <span className="ml-2 text-sm md:text-md xl:text-2xl text-gray-600">({ Math.round(selectedProduct.rating) } out of 5)</span>
            </div>
            { selectedProduct.dimensions &&
              <div className='mt-8'>
                <h4 className="text-md font-semibold text-gray-900">Dimensions</h4>
                <p className="text-gray-700 text-sm">Height : { selectedProduct.dimensions.height }</p>
                <p className="text-gray-700 text-sm">Width : { selectedProduct.dimensions.width }</p>
                <p className="text-gray-700 text-sm">Depth : { selectedProduct.dimensions.depth }</p>
              </div>
            }
            { selectedProduct.weight &&
              <div>
                <h4 className="text-md font-semibold text-gray-900">Weight : { selectedProduct.weight }</h4>
              </div>
            }

          </div>
          <div>
            <button
              type="button"
              onClick={ handleAddToCart }
              disabled={ selectedProduct.stock < 1 }
              className={ `w-full mt-6 px-6 py-3 rounded-md text-white text-lg font-medium transition ${selectedProduct.stock < 1
                ? 'bg-gray-400 cursor-not-allowed'
                : 'bg-indigo-600 hover:bg-indigo-700'
                }` }
            >
              { selectedProduct.stock < 1 ? 'Out of Stock' : 'Add to Cart' }
            </button>
          </div>
        </div>
      </div>

      {/* Bottom Section: Description + Reviews/Policy */ }
      <div className="grid grid-cols-1 md:grid-cols-2 gap-8 mt-12 border-t pt-8">
        {/* Product Description */ }
        <div>
          <h3 className="text-lg font-semibold text-gray-800 mb-4">Product Description</h3>
          <p className="text-gray-700 text-base">{ selectedProduct.description }</p>
          <div className="mt-4">
            <h4 className="text-md font-semibold text-gray-800">Warranty</h4>
            <p className="text-gray-700 text-sm">{ selectedProduct.warrantyInformation }</p>
            <div className="mt-6">
              <h4 className="text-md font-semibold text-gray-800">Return Policy</h4>
              <p className="text-gray-700 text-sm">{ selectedProduct.returnPolicy }</p>
            </div>

          </div>

        </div>

        {/* Reviews, Return Policy & Warranty */ }
        <div>
          <h3 className="text-lg font-semibold text-gray-800 mb-4">Customer Reviews</h3>
          <div className="space-y-4">
            { selectedProduct.reviews && selectedProduct.reviews.length > 0 ? (
              selectedProduct.reviews.map((review, idx) => (
                <div key={ idx } className="bg-gray-100 p-4 rounded-lg shadow-sm">
                  <div className="flex items-center justify-between mb-1">
                    <h4 className="text-sm font-medium text-gray-800">{ review.reviewerName }</h4>
                    <span className="text-xs text-gray-500">{ new Date(review.date).toLocaleDateString() }</span>
                  </div>
                  <div className="flex items-center mb-2">
                    { [0, 1, 2, 3, 4].map((_, index) => (
                      <StarIcon
                        key={ index }
                        className={ `h-4 w-4 ${review.rating > index ? 'text-yellow-500' : 'text-gray-300'}` }
                      />
                    )) }
                  </div>
                  <p className="text-gray-700 text-sm">{ review.comment }</p>
                </div>
              ))
            ) : (
              <p className="text-gray-500">No reviews yet.</p>
            ) }
          </div>


        </div>
      </div>
    </div>
  ) : null;
}
