import { useState, useEffect } from "react";
import UserAddressForm from "./UserAddressForm";

export default function UserAddressFormModal({
    modalData,
    setModalData,
    data,
    handleAddAddress,
    handleEditAddress,
}) {
    const [localData, setLocalData] = useState(null);

    useEffect(() => {
        if (modalData.addOrEdit === "edit" && data) {
            setLocalData(data); // Prefill form with existing data for editing
        } else if (modalData.addOrEdit === "add") {
            setLocalData(null); // Clear form for adding new address
        }
    }, [modalData, data]);

    return (
        modalData.isOpen && (
            <div className="fixed inset-0 bg-black bg-opacity-40 flex items-center justify-center z-50">
                <div className="bg-white rounded-lg shadow-lg p-6 w-full max-w-3xl overflow-y-auto max-h-[90vh]">
                    <h2 className="text-2xl font-bold mb-4">
                        { modalData.addOrEdit === "add" ? "Add New Address" : "Edit Address" }
                    </h2>

                    <UserAddressForm
                        modalData={ modalData }
                        setModalData={ setModalData }
                        data={ localData }
                        handleAddAddress={ handleAddAddress }
                        handleEditAddress={ handleEditAddress }
                    />
                </div>
            </div>
        )
    );
}
