import { useSelector, useDispatch } from "react-redux";
import { Navigate } from "react-router-dom";
import Avatar from "react-avatar";
import { selectLoggedInUserInfo, selectUserInfoLoaded, updateUserAsync, selectUserSliceErrors } from "../userSlice";
import { React, useState, useEffect } from "react";
import { toast } from "react-hot-toast";
import ConfirmDeleteModal from "../../comman/Modal";
import UserAddressFormModal from "./UserAddressFormModal";
import { PencilSquareIcon } from "@heroicons/react/20/solid";
import EditUserInfoModal from "./UserInfoEditModal";

function UserProfile() {
    const [editIndex, setEditIndex] = useState(null);
    const [modalData, setModalData] = useState({
        isOpen: false,
        addOrEdit: null,
    });

    const [data, setData] = useState({});
    const [editUserDetailModalOpen, setEditUserDetailModalOpen] = useState(false);
    const [indexToDelete, setIndexToDelete] = useState(null);
    const [confirmDeleteModalOpen, setConfirmDeleteModalOpen] = useState(false);

    const dispatch = useDispatch();
    const userInfoLoaded = useSelector(selectUserInfoLoaded);
    const userInfo = useSelector(selectLoggedInUserInfo);
    const userSliceError = useSelector(selectUserSliceErrors)

    useEffect(() => {
        // Reset states when modal is closed
        if (!modalData.isOpen) {
            setData({});
        }
    }, [modalData.isOpen]);

    useEffect(() => {
        if (Object.keys(data).length > 0 && editIndex !== null) {
            setModalData({
                addOrEdit: "edit",
                isOpen: true,
            });
        }
    }, [data, editIndex]);

    useEffect(() => {
        if (userSliceError && typeof userSliceError === 'string') {
            toast.error(userSliceError);
        }
    }, [userSliceError]);


    function handleConfirmDelete(indexToDelete) {
        const newAddresses = userInfo.addresses.filter((_, i) => i !== indexToDelete);
        const updatedUser = { ...userInfo, addresses: newAddresses };
        dispatch(updateUserAsync({ update: updatedUser, toast, type: "REMOVE" }));
        setConfirmDeleteModalOpen(false);
        setIndexToDelete(null);

    }

    function handleCancleDelete() {
        setConfirmDeleteModalOpen(false);
        setIndexToDelete(null);
    }

    function handleRemove(index) {
        if (userInfo.addresses.length > 1) {
            setIndexToDelete(index);
            setConfirmDeleteModalOpen(true);

        } else {
            toast.error("At least one Shipping Address is needed");
            return;
        }


    }


    function handleAddAddress(data) {
        const updatedUser = {
            ...userInfo,
            addresses: [...userInfo.addresses, data],
        };
        dispatch(updateUserAsync({ update: updatedUser, toast, type: "ADD" }));
    }

    function handleEditForm(index) {
        setEditIndex(index);
        setData({ ...userInfo.addresses[index] });
    }
    function handleEditAddress(editedAddress) {
        const updatedAddresses = [...userInfo.addresses];
        updatedAddresses.splice(editIndex, 1, editedAddress);

        const updatedUser = { ...userInfo, addresses: updatedAddresses };
        dispatch(updateUserAsync({ update: updatedUser, toast, type: "EDIT" }));

        setEditIndex(null);
        setModalData({ ...modalData, isOpen: false });
    }

    function handleUpdateUserInfo() {
        setEditUserDetailModalOpen(true);

    }

    function handleConfirmEditUser(editedUserInfo) {

        dispatch(updateUserAsync({ update: editedUserInfo, toast, type: "EDIT" }));
        setEditUserDetailModalOpen(false);

    }

    function handleCancleEditUser() {
        setEditUserDetailModalOpen(false)
    }

    return (userInfo && !userSliceError) ? (
        <div className="bg-gray-100 min-h-screen flex justify-center">

            <EditUserInfoModal

                isOpen={ editUserDetailModalOpen }
                onClose={ handleCancleEditUser }
                initialUserInfo={ userInfo }
                onSave={ handleConfirmEditUser }

            />

            <ConfirmDeleteModal
                title="Confirm Delete"
                message={ `Delete : Shipping Address ${indexToDelete + 1}` }
                isOpen={ confirmDeleteModalOpen }
                buttonOne="Confirm"
                buttonTwo="Cancel"
                buttonOneHandler={ () => { handleConfirmDelete(indexToDelete) } }
                buttonTwoHandler={ handleCancleDelete }
            />
            <UserAddressFormModal
                modalData={ modalData }
                setModalData={ setModalData }
                data={ data }
                handleAddAddress={ handleAddAddress }
                handleEditAddress={ handleEditAddress }
            />
            {/* Display User Information */ }
            <div className="bg-white shadow-md rounded-lg p-8 max-w-md w-full flex flex-col items-center">
                <Avatar
                    name={ userInfo.username }
                    size="100"
                    round={ true }
                    className="cursor-pointer mx-auto mb-4"
                    style={ { fontSize: "48px" } }
                />

                <div className="w-full">
                    <div className="flex justify-end">
                        <PencilSquareIcon
                            className="w-4 h-4 text-gray-500 cursor-pointer"
                            onClick={ () => {
                                handleUpdateUserInfo();
                            } }
                        />
                    </div>

                    <div className="flex justify-center items-center">
                        <h1 className="text-2xl font-semibold">{ userInfo.username }</h1>

                    </div>
                    <p className="text-gray-600 text-center">{ userInfo.email }</p>

                </div>


                <div className="border-t border-gray-300 flex flex-col justify-center w-full">
                    <h1 className="text-2xl my-2 font-semibold">Shipping addresses</h1>
                    { userInfo.addresses.map((address, index) => (
                        <div key={ index } className="my-5">
                            <h1 className="font-bold">Shipping Address { index + 1 }</h1>
                            { Object.entries(address).map(([key, value], index) => (
                                <p className="text-gray-600" key={ index }>
                                    { key }: { value }
                                </p>
                            )) }
                            <button
                                onClick={ () => {
                                    handleRemove(index);
                                } }
                                type="button"
                                className="font-medium text-indigo-600 hover:text-indigo-500"
                            >
                                Remove
                            </button>
                            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                            <button
                                onClick={ () => {
                                    handleEditForm(index);
                                } }
                                type="button"
                                className="font-medium text-indigo-600 hover:text-indigo-500"
                            >
                                Edit
                            </button>
                        </div>
                    )) }
                </div>
                <button
                    className="w-full rounded-md bg-indigo-600 px-3.5 py-2.5 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                    onClick={ () => {
                        setModalData({ ...modalData, isOpen: true, addOrEdit: "add" });
                    } }
                >
                    Add New Address
                </button>
                { userInfo.addresses.length < 1 && (
                    <>
                        <p className="text-gray-500 text-center mt-10">No Addresses Add Now</p>
                        <button
                            onClick={ () => {
                                setModalData({ ...modalData, isOpen: true, addOrEdit: "add" });
                            } }
                        >
                            <svg
                                xmlns="http://www.w3.org/2000/svg"
                                fill="none"
                                viewBox="0 0 24 24"
                                strokeWidth={ 1.5 }
                                stroke="currentColor"
                                className="w-6 h-6"
                            >
                                <path
                                    strokeLinecap="round"
                                    strokeLinejoin="round"
                                    d="M12 4.5v15m7.5-7.5h-15"
                                />
                            </svg>
                        </button>
                    </>
                ) }
            </div>
        </div>
    ) : userInfoLoaded ? (
        <Navigate to="/login" />
    ) : null;
}

export default UserProfile;
