import { useEffect, useState } from 'react';
import { ArrowUpIcon } from '@heroicons/react/24/outline';

export default function ScrollToTopButton() {
    const [isVisible, setIsVisible] = useState(false);

    useEffect(() => {
        const toggleVisibility = () => {
            if (window.scrollY > window.innerHeight + 500) {
                setIsVisible(true);
            } else {
                setIsVisible(false);
            }
        };

        window.addEventListener('scroll', toggleVisibility);
        return () => window.removeEventListener('scroll', toggleVisibility);
    }, []);

    const scrollToTop = () => {
        window.scrollTo({
            top: 0,
            behavior: 'smooth',

        });
    };

    if (!isVisible) return null;

    return (
        <button
            onClick={ scrollToTop }
            className="fixed bottom-6 right-6 z-50 flex items-center justify-center rounded-full bg-gray-800 p-3 text-gray-200 shadow-lg transition hover:bg-gray-700 focus:ring-2 focus:ring-white focus:ring-offset-2 focus:ring-offset-gray-800"
            aria-label="Scroll to top"
        >
            <ArrowUpIcon className="h-5 w-5" />
        </button>
    );
}
